.top-nav {
  margin-bottom: 0;
  padding: 0;
}

.top-nav__item {
  display: inline-block;
  position: relative;
}

.top-nav__item:after {
  content: '';
  display: block;
  position: absolute;
  top: 20px;
  right: -2px;
  width: 1px;
  bottom: 20px;
  background: url("../img/nav-separator.png") no-repeat;
}

.top-nav__item:last-child:after {
  display: none;
}

.top-nav__item .child {
  display: none;
}

.top-nav__link {
  font-weight: 100;
  color: rgba(255, 255, 255, 0.7);
  display: block;
  padding: 20px 30px;
  border-bottom: 3px solid rgba(51, 177, 133, 0);
  transition: .2s;
}

.top-nav__link:hover, .top-nav__link:focus, .top-nav__link:visited {
  cursor: pointer;
  color: #fff;
  text-decoration: none;
  border-color: #33b185;
}

.top-nav__item.active .top-nav__link {
  color: #33b185;
}

.top-nav__item.active:before {
  content: '';
  display: block;
  position: absolute;
  bottom: 3px;
  left: 50%;
  margin-left: -5px;
  z-index: 99;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 8px 8px 8px;
  border-color: transparent transparent #33b185 transparent;
  transition: .2s;
}

.top-nav__item.active .child {
  border-top: 3px solid #33b185;
  display: block;
  position: absolute;
  padding: 0;
  margin: 0;
  margin-top: -3px;
  z-index: 22;
  background: #494e59;
  animation-duration: .5s;
}

.top-nav__item.active .child__item {
  list-style: none;
  text-align: left;
  white-space: nowrap;
}

.top-nav__item.active .child__link {
  font-weight: 100;
  color: rgba(255, 255, 255, 0.7);
  display: block;
  padding: 10px 50px 10px 30px;
  background: rgba(51, 177, 133, 0);
  transition: .2s;
}

.top-nav__item.active .child__link:hover, .top-nav__item.active .child__link:focus, .top-nav__item.active .child__link:visited {
  cursor: pointer;
  color: #fff;
  text-decoration: none;
  background: #33b185;
}

