.how-to-use__title {
  font-weight: 700;
  color: #393d45;
  text-align: center;
  text-transform: uppercase;
  font-size: 20px;
  display: block;
  max-width: 180px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.how-to-use__text {
  color: #707ea0;
}

.maps {
  background: rgba(57, 61, 69, 0.02);
}

.maps__title {
  font-weight: 400;
  color: #393d45;
  text-align: center;
  margin: 0 0 50px 0;
  text-transform: uppercase;
}

.maps__title span {
  color: #33b185;
  font-weight: 700;
}

.maps .animated {
  animation-duration: .6s;
}

.history {
  background: radial-gradient(#505661, #393d45);
  padding: 50px 0;
}

.history__button {
  background: #fff;
  width: 80px;
  height: 80px;
  display: inline-flex;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 700;
  color: #393d45;
  margin: 0 40px;
  position: relative;
  transition: .2s;
}

.history__button:after {
  content: '';
  display: block;
  position: absolute;
  border: 2px solid rgba(255, 255, 255, 0.5);
  width: 96px;
  height: 96px;
  border-radius: 50%;
  left: -8px;
  top: -8px;
  transition: .3s;
}

.history__button:before {
  content: '';
  display: block;
  position: absolute;
  border: 1px solid rgba(255, 255, 255, 0.3);
  width: 110px;
  height: 110px;
  border-radius: 50%;
  left: -15px;
  top: -15px;
  transition: .4s;
}

.history__button:hover, .history__button:focus, .history__button:active {
  text-decoration: none;
  background: #33b185;
  color: #fff;
}

.history__button:hover:after, .history__button:focus:after, .history__button:active:after {
  border-color: rgba(51, 177, 133, 0.5);
}

.history__button:hover:before, .history__button:focus:before, .history__button:active:before {
  border-color: rgba(51, 177, 133, 0.2);
}

.history__button.active {
  background: #33b185;
  color: #fff;
  transform: scale(1.2);
}

.history__button.active:after {
  border-color: rgba(51, 177, 133, 0.5);
}

.history__button.active:before {
  border-color: rgba(51, 177, 133, 0.2);
}

.history__main-title {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 60px;
  font-weight: 700;
}

.history__text {
  margin-top: 50px;
  color: rgba(204, 209, 221, 0.8);
  font-size: 15px;
  font-weight: 100;
  display: none;
}

.history__text.active {
  display: block;
}

.main-map {
  margin-top: 60px;
}

.land {
  fill: #ccd1dd;
  fill-opacity: 1;
  stroke: #fff;
  stroke-opacity: 1;
  stroke-width: 0.5;
  transition: .2s;
}

.active-land {
  fill: #808caa;
}

.active-land:hover {
  fill: #288967;
  stroke: #288967;
  cursor: pointer;
}

.maps {
  padding: 40px 0;
}

.maps-title {
  margin-bottom: 60px;
}

.pink {
  shape-rendering: crispEdges;
  fill: #288967;
}

#maps-content {
  overflow: hidden;
  position: relative;
}

.map-content {
  transition: .2s ease-in-out;
}

#maps-content.map-content {
  transform: translateY(-100%);
}

#maps-content.active {
  overflow: auto;
  position: relative;
}

#maps-content.active .map-content {
  transform: translateY(0);
}

#map-title {
  margin-top: 30px;
  color: #fff;
}

#map-content {
  margin-top: 10px;
  padding: 0;
}

#map-content li {
  list-style: none;
}

#map-content li a {
  color: #33b185;
}

#map-data {
  display: none;
  width: 300px;
  margin-left: -150px;
  height: 200px;
  position: absolute;
  background: #393d45;
  border: 3px solid #393d45;
  border-radius: 4px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  transition: .2s ease-in-out;
}

#map-data:after {
  content: '';
  display: block;
  position: absolute;
  top: -13px;
  left: 140px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 10px 10px 10px;
  border-color: transparent transparent #393d45 transparent;
}

#map-data__close {
  position: absolute;
  right: 10px;
  top: 10px;
  width: 30px;
  height: 30px;
  background: #d4495a;
  color: #fff;
  border-radius: 50%;
  transition: .2s ease-in-out;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  text-center: center;
  outline: none;
}

#map-data__close:hover {
  background: #bd2d3e;
  transform: scale(1.2) rotate(360deg);
}

#map-data__close i {
  line-height: 1px;
}

#canvas {
  position: absolute;
  /*background: rgba(221, 238, 238, 0.25);*/
}

.headings__box {
  display: inline-block;
  width: 100%;
  max-width: 250px;
  height: 190px;
  padding: 25px 0px;
  border: 1px solid transparent;
  color: #5d626e;
}

.headings__box:hover, .headings__box:focus {
  text-decoration: none;
  color: #5d626e;
  border: 1px solid #8c8c8c;
}

